}

[data-tab-content] {
  display: none !important;
}
[data-tab-content].active {
  display: block !important;
}

.dis-scroll {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  background-attachment: fixed;
  overscroll-behavior: none !important;
  scroll-behavior: none !important;
}

.page--ios .dis-scroll {
  position: relative;
}

.overlay {
  position: fixed;
  height: 100%;
  right: 0;
  bottom: 0;
  left: 0;
  top: 0;
  z-index: 1;
  padding: 20px 20px;
  overflow-y: scroll;
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-rows: 1fr;
  visibility: hidden;
  opacity: 0;
  background: rgba(8, 20, 40, 0.87) linear-gradient(180deg, rgba(104, 102, 246, 0) 0%, rgba(104, 102, 246, 0.21) 100%);
  transition: all 0.6s;
  transition-delay: 0.5s;
  pointer-events: none;
}
.overlay::-webkit-scrollbar {
  display: none;
}
.overlay.none {
  display: none !important;
}
.overlay.active {
  position: fixed;
  z-index: 20;
  visibility: visible;
  opacity: 1;
  transition: all 0.6s;
  pointer-events: initial;
}
.overlay.active--mode {
  z-index: 8;
  background: rgb(8, 20, 40) linear-gradient(180deg, rgba(104, 102, 246, 0) 0%, rgba(104, 102, 246, 0.21) 100%);
}
.overlay.active--mode .modal {
  transition: all 0s !important;
  animation: none;
}
@media (max-width: 767px) {
  .overlay.active--mode {
    z-index: 10;
    align-items: center;
  }
}

*::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  border-radius: 5px;
  cursor: pointer;
}
@media (max-width: 767px) {
  *::-webkit-scrollbar {
    display: none;
  }
}
*::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar-thumb);
  cursor: pointer;
  transition: all 0.2s;
}
*::-webkit-scrollbar-track {
  background-color: var(--scrollbar-track);
}
*::-webkit-scrollbar-button {
  display: none;
  background-color: transparent;
  box-shadow: none;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.modal {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0vh);
  opacity: 0;
  grid-row: 1;
  grid-column: 1;
  transition: opacity 1s;
  pointer-events: none;
}
.modal.active {
  position: relative;
  transform: translate(-50%, 0);
  opacity: 1;
  z-index: 20;
  transition: opacity 1s;
  pointer-events: initial;
}
.modal__close {
  position: absolute;
  right: 12px;
  top: 12px;
  z-index: 20;
}
.modal .close svg {
  pointer-events: none;
}

