.custom-icon {
  display: flex;
  width: 20px;
  height: 20px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  pointer-events: none;
}
.custom-icon--star {
  width: 40px;
  height: 20px;
  background: var(--star-icon);
}
.custom-icon--stars {
  width: 30px;
  height: 30px;
  position: absolute;
  overflow: hidden;
}
.custom-icon--stars::before {
  content: "";
  height: 1000px;
  background: var(--stars-icon);
  background-repeat: repeat;
  position: absolute;
  width: 100%;
  z-index: 1;
  animation: stars 80s linear infinite;
}
.custom-icon--stars::after {
  position: absolute;
  content: "";
  top: -2px;
  z-index: 2;
  width: 100%;
  height: 7px;
  background: var(--gradient-b);
}
.custom-icon--nav {
  background-image: var(--custom-stars-nav);
  width: 92px;
  height: 50px;
}

.logo {
  padding-right: 20px;
  border-right: 1px solid var(--br-b);
}

.earn-planets {
  border-radius: 90px;
  background-image: url(../img/sprite/earn-bg2.svg), var(--gradient-a);
  padding: 10px 20px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 100%;
  color: rgba(var(--cl-d), 1);
}
.earn-planets .custom-icon {
  position: absolute;
  right: 1px;
  top: -5px;
}

.blue-btn {
  background: rgba(var(--bg-e), 1);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 4px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  text-transform: capitalize;
  color: rgba(var(--cl-a), 1);
}
.blue-btn svg {
  fill: transparent;
}

.more-btn {
  width: 40px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  border: 1px solid var(--br-a);
  border-radius: 12px;
  color: rgba(var(--cl-a), 1);
  transition: all 0.4s;
}
.more-btn svg {
  fill: currentColor;
  transition: inherit;
}
.more-btn.active {
  background-color: rgba(var(--bg-e), 1);
  border-color: var(--br-d);
}

.read-more {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 100%;
  color: rgba(var(--cl-f), 1);
  transition: color 0.4s;
  display: flex;
  align-items: center;
  grid-gap: 4px;
}
.read-more svg {
  fill: transparent;
  stroke: currentColor;
}

.click-triger {
  display: flex;
  align-items: center;
  grid-gap: 8px;
  justify-content: center;
  width: fit-content;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 112%;
  color: rgba(var(--cl-f), 1);
}
.click-triger svg {
  fill: transparent;
}

.blue-text {
  color: rgba(var(--cl-e), 1);
}

.green-text {
  color: rgba(var(--cl-m), 1);
}

.back-news {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 5px;
  max-width: 141px;
  width: 100%;
  height: 52px;
  background: rgba(var(--bg-g), 1);
  border-radius: 12px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 14px;
  text-transform: capitalize;
  color: rgba(var(--cl-a), 1);
}

.copy-link {
  height: 46px;
  width: 145px;
  background: rgba(var(--bg-k), 1);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 6px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 100%;
  color: rgba(var(--cl-a), 1);
}
.copy-link svg {
  fill: transparent;
  stroke: currentColor;
}

.backstory-btn {
  --button-height: 54px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 220px;
  height: var(--button-height);
  grid-gap: 10px;
  background: linear-gradient(226.99deg, rgba(79, 99, 199, 0.4) 0%, rgba(96, 191, 238, 0.4) 100%);
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 100%;
  color: rgba(var(--cl-a), 1);
  border-radius: 12px;
}

.mining-btn {
  width: 164px;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 4px;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 100%;
  text-transform: capitalize;
  color: rgba(var(--cl-d), 1);
  background: rgba(var(--bg-c), 1);
  box-shadow: inset 0px 0px 14px rgba(148, 163, 184, 0.1);
  border-radius: 12px;
}
.mining-btn svg {
  fill: transparent;
  stroke: currentColor;
}

.convert-btn {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: rgba(var(--bg-v), 1);
  color: rgba(var(--cl-d), 1);
  border: 1px solid rgba(var(--bg-v), 1);
}
.convert-btn svg {
  fill: transparent;
  stroke: currentColor;
}

.red-shadow > * {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 7.99501px 7.99501px rgba(252, 7, 12, 0.3);
  border-radius: 50%;
  width: fit-content;
}

.blue-shadow > * {
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 7.99501px 7.99501px rgba(27, 119, 232, 0.32);
  border-radius: 50%;
  width: fit-content;
}

.green-text {
  color: rgba(var(--cl-m), 1) !important;
}

.red-text {
  color: rgba(var(--cl-o), 1) !important;
}

.yellow-text {
  color: rgba(var(--cl-q), 1) !important;
}

.main-top {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 10px;
}
.main-top P + * {
  margin-top: 10px;
}
.main-top__title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 100%;
  text-transform: capitalize;
  color: rgba(var(--cl-a), 1);
  position: relative;
}
.main-top p {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: rgba(var(--cl-f), 1);
}
.main-top__btn {
  width: 220px;
  height: 48px;
}

.main-nav__list {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  grid-gap: 20px;
}
.main-nav__link {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 100%;
  color: rgba(var(--cl-d), 1);
  display: flex;
  align-items: center;
  grid-gap: 5px;
  position: relative;
  transition: 0.2s linear;
}
.main-nav__link .custom-icon {
  position: absolute;
}
.main-nav__link .custom-icon--star {
  bottom: 60%;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0.4;
  transition: inherit;
}
.main-nav__link svg {
  fill: transparent;
  stroke: currentColor;
}
.main-nav__link:hover, .main-nav__link.active {
  color: rgba(var(--cl-a), 1);
}
.main-nav__link:hover .custom-icon, .main-nav__link.active .custom-icon {
  opacity: 1;
}

.hero-info {
  display: grid;
  grid-template-columns: repeat(2, auto);
  max-width: 400px;
  grid-gap: 40px;
}
.hero-info__item {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 6px 14px;
}
.hero-info__icon {
  grid-row: 1/span 2;
}
.hero-info__title, .hero-info__value {
  grid-column: 2;
}
.hero-info__title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 129%;
  color: rgba(var(--cl-f), 1);
}
.hero-info__value {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 21px;
  line-height: 100%;
  color: rgba(var(--cl-a), 1);
}

.hero-statistics {
  --gap: 60px;
  display: flex;
  grid-gap: var(--gap);
  padding: 21px 40px;
  background: rgba(var(--bg-g), 1);
  border-radius: 18px;
  justify-content: center;
  position: relative;
}
.hero-statistics::before {
  top: 0;
  bottom: -10px;
  left: 10px;
  right: 10px;
  content: "";
  position: absolute;
  z-index: -1;
  display: block;
  background: rgba(var(--bg-g), 0.3);
  border-radius: 18px;
}
.hero-statistics__item {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 2px 10px;
  position: relative;
  align-items: center;
}
.hero-statistics__item:not(:last-child)::before {
  position: absolute;
  width: 1px;
  height: 100%;
  content: "";
  background-color: var(--br-b);
  right: calc(var(--gap) / 2 * -1);
  transform: translateX(50%);
  border-radius: 1px;
}
.hero-statistics__icon {
  grid-row: 1/span 2;
}
.hero-statistics__title, .hero-statistics__value {
  grid-column: 2;
}
.hero-statistics__title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 129%;
  color: rgba(var(--cl-f), 1);
}
.hero-statistics__value {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 21px;
  line-height: 100%;
  color: rgba(var(--cl-a), 1);
}

.main-slider .swiper-container {
  max-width: 496px;
  width: 100%;
  background-color: rgba(var(--bg-g), 1);
  background-image: var(--main-slider-icon);
  border-radius: 24px;
  height: 100%;
}
.main-slider .swiper-slide {
  padding-top: min(5.455vw, 60px);
  width: fit-content;
  display: flex;
  align-items: flex-end;
}
.main-slider .swiper-slide.swiper-slide-active picture, .main-slider .swiper-slide.swiper-slide-duplicate-active picture {
  transform: translateY(0px);
}
.main-slider .swiper-slide picture {
  display: grid;
  transition: transform 2s ease;
  transform: translateY(110px);
}

.article-social {
  --item-size: 36px;
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
}
.article-social__list {
  display: flex;
  align-items: center;
  grid-gap: 14px;
}
.article-social__title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  color: rgba(var(--cl-f), 1);
}
.article-social__link {
  width: var(--item-size);
  height: var(--item-size);
  background: rgba(var(--bg-k), 1);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.question-block {
  max-width: 229px;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 10px;
}
.question-block svg {
  fill: transparent;
}
.question-block * {
  grid-column: 1/span 2;
}
.question-block__icon {
  grid-column: 1;
  grid-row: 1/span 2;
}
.question-block__title {
  grid-column: 2;
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 125%;
  color: rgba(var(--cl-a), 1);
}
.question-block__more {
  grid-column: 2;
}

.benefits-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
  grid-gap: 20px;
  width: 100%;
}
.benefits-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.benefits-list__title {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 120%;
  text-align: center;
  text-transform: capitalize;
  color: rgba(var(--cl-a), 1);
}
.benefits-list__descr {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 105%;
  text-align: center;
  color: rgba(var(--cl-f), 1);
}
.benefits-list__title + .benefits-list__descr {
  margin-top: 10px;
}

.program-slider {
  width: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: 30px;
}
.program-slider .swiper-slide {
  height: initial;
  max-width: 840px;
  transition: transform 6s, opacity 2s;
}
.program-slider .swiper-slide.swiper-slide-prev, .program-slider .swiper-slide.swiper-slide-duplicate-prev {
  transform: scale(0);
  opacity: 0;
}
.program-slider .swiper-slide.swiper-slide-next {
  transition: none !important;
}
.program-slider .swiper-wrapper {
  align-items: stretch;
}
.program-slider__wrapper {
  padding: 22px 0 40px;
}
.program-slider__slide {
  display: flex;
  height: 100%;
  padding: 40px;
  border-radius: 24px;
  overflow: hidden;
  max-width: 840px;
  width: 100%;
  background-color: rgba(var(--bg-g), 1);
  position: relative;
}
.program-slider__slide::before {
  width: 347px;
  height: 347px;
  background: var(--gradient-c);
  filter: blur(57px);
  border-radius: 50%;
  position: absolute;
  content: "";
  right: 0;
  top: 50%;
  transform: translate(100px, -50%);
}
.program-slider__inner {
  max-width: 460px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 15px;
}
.program-slider__image {
