.roadmap-proof {
  padding: 20px 0 100px;
  background-image: url(../img/roadmap-bg.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: bottom;
}
.roadmap-proof__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  grid-gap: 40px;
}
.roadmap-proof__list {
  width: 100%;
  counter-reset: value;
  display: flex;
  justify-content: space-between;
  padding-bottom: 110px;
  position: relative;
}
.roadmap-proof__list::before {
  left: 60px;
  right: 60px;
  height: 3px;
  position: absolute;
  content: "";
  background-image: var(--roadmap-line);
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 3px;
  background-size: 100% 100%;
  bottom: 50%;
  pointer-events: none;
}
.roadmap-proof__list::after {
  left: 60px;
  right: 60px;
  height: 100%;
  background-image: var(--roadmap-wave);
  background-size: 100% 100%;
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  content: "";
  pointer-events: none;
}
.roadmap-proof__item {
  width: 120px;
  --size: 54px;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: var(--size);
  position: relative;
}
.roadmap-proof__item:nth-child(even) {
  flex-direction: column-reverse;
  transform: translateY(56%);
}
.roadmap-proof__item:nth-child(even) .roadmap-proof__item-row {
  flex-direction: inherit;
}
.roadmap-proof__item-row {
  width: fit-content;
  display: flex;
  align-items: center;
  flex-direction: column;
  grid-gap: 6px;
}
.roadmap-proof__item::before {
  width: 3px;
  height: var(--size);
  top: 50%;
  background-image: var(--roadmap-dashed);
  position: absolute;
  content: "";
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
.roadmap-proof__descr {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 100%;
  text-align: center;
  text-transform: capitalize;
  color: #D3D3D3;
  height: 24px;
  display: flex;
  align-items: flex-end;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.roadmap-proof__value {
  position: relative;
  z-index: 1;
}
.roadmap-proof__value::before {
  position: absolute;
  counter-increment: value;
  content: counters(value, "");
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 100%;
  color: rgba(var(--cl-a), 1);
  pointer-events: none;
}
.roadmap-proof__value::after {
  position: absolute;
  width: 82px;
  height: 82px;
  background-image: var(--roadmap-value-bg);
  background-size: 100% 100%;
  content: "";
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: -2;
  pointer-events: none;
}
.roadmap-proof__icon {
